interface Cocomo2PropsAdvance { KLoC: number }

export const CocomoCalc2Advance: React.FC<Cocomo2PropsAdvance> = ({ KLoC }) => {
  
  const [drivers, setDrivers] = useState(InitialValue);
  const isMobile = useMediaQuery("(max-width: 400px)");

  const handleUpdate = (driver: string, newValue: number) =>
    setDrivers({ ...drivers, [driver]: newValue });

  const res = useMemo(() => calculateCocomo2Advance(KLoC, drivers), [
    drivers,
    KLoC,
  ]);

  return (
    <>
    <Grid1>
      <ResultColumn title={Round(res.PM)} label="–¢—Ä—É–¥–æ–µ–º–∫–æ—Å—Ç—å(PM), —á–µ–ª.√ó –º–µ—Å" />
      <ResultColumn title={Round(res.TM)} label="–í—Ä–µ–º—è —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–∏(TM), –º–µ—Å" />
    </Grid1>

      <hr />

      <Columns>
        {isMobile ? (
          <>
            <div>–û—á. –Ω–∏–∑.</div>
            <div>–ù–∏–∑.</div>
            <div>–°—Ä–µ–¥.</div>
            <div>–í—ã—Å.</div>
            <div>–û—á. –≤—ã—Å.</div>
            <div>–ö—Ä–∏—Ç. –≤—ã—Å.</div>
          </>
        ) : (
          <>
            <div>–û—á–µ–Ω—å –Ω–∏–∑–∫–∏–π</div>
            <div>–ù–∏–∑–∫–∏–π</div>
            <div>–°—Ä–µ–¥–Ω–∏–π</div>
            <div>–í—ã—Å–æ–∫–∏–π</div>
            <div>–û—á–µ–Ω—å –≤—ã—Å–æ–∫–∏–π</div>
            <div>–ö—Ä–∏—Ç–∏—á–µ—Å–∫–∏ –≤—ã—Å–æ–∫–∏–π</div>
          </>
        )}
      </Columns>

      <Grid>
        {driversKeys.map(({ key, text }) => (
          <RadioGroup
            title={text}
            selectRadio={handleUpdate}
            value={drivers[key]}
            radioValues={costDriversCocomo2Advance[key]}
            group={key}
            key={key}
          />
        ))}
      </Grid>
    </>
  );
};

