/** –ö–æ–Ω—Å—Ç–∞–Ω—Ç—ã, –∫–æ—Ç–æ—Ä—ã–µ –Ω—É–∂–Ω—ã –¥–ª—è —Ä–∞—Å—á—ë—Ç–∞ –±–∞–∑–æ–≤–æ–π –º–æ–¥–µ–ª–∏ COCOMO */
export interface modeCoefficients {
  ab: number;
  bb: number;
  cb: number;
  db: number;
  ai: number;
  bi: number;
}

/** –û—Ä–≥–∞–Ω–∏—á–µ—Å–∫–∏–π (Organic mode) ‚Äì –º–∞–ª–µ–Ω—å–∫–∏–µ –∫–æ–º–∞–Ω–¥—ã —Å —Ö–æ—Ä–æ—à–∏–º –æ–ø—ã—Ç–æ–º —Ä–∞–±–æ—Ç—ã –∏ –Ω–µ –∂–µ—Å—Ç–∫–∏–º–∏ —Ç—Ä–µ–±–æ–≤–∞–Ω–∏—è–º–∏ –∫ —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–µ */
export const Organic: modeCoefficients = {
  ab: 2.4,
  bb: 1.05,
  cb: 2.5,
  db: 0.38,
  ai: 3.2,
  bi: 1.05
};

/**  –ü–æ–ª—É—Ä–∞–∑–¥–µ–ª–µ–Ω–Ω—ã–π –≤–∏–¥ (Intermediate/Semi-detached mode) ‚Äì —Å—Ä–µ–¥–Ω–∏–µ –ø–æ —Ä–∞–∑–º–µ—Ä—É –∫–æ–º–∞–Ω–¥—ã
 * —Å–æ —Å–º–µ—à–∞–Ω–Ω—ã–º –æ–ø—ã—Ç–æ–º —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–∏ –∏ —Å–æ —Å–º–µ—à–∞–Ω–Ω—ã–º–∏ —Ç—Ä–µ–±–æ–≤–∞–Ω–∏—è–º–∏ (–∫–∞–∫ –∂–µ—Å—Ç–∫–∏–º–∏, —Ç–∞–∫ –∏ –Ω–µ—Ç). */
export const Semidetach: modeCoefficients = {
  ab: 3,
  bb: 1.12,
  cb: 2.5,
  db: 0.35,
  ai: 3,
  bi: 1.12
};

/** –í—Å—Ç—Ä–æ–µ–Ω–Ω—ã–π –≤–∏–¥ (Intered/Embedded mode) ‚Äì —Ä–∞–∑—Ä–∞–±–∞—Ç—ã–≤–∞—é—Ç—Å—è —Å —É—á–µ—Ç–æ–º –º–Ω–æ–∂–µ—Å—Ç–≤–∞ –∂–µ—Å—Ç–∫–∏—Ö –æ–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–π
 * (–ø–æ –∞–ø–ø–∞—Ä–∞—Ç–Ω–æ–º—É, –ø—Ä–æ–≥—Ä–∞–º–º–Ω–æ–º—É, –æ–ø–µ—Ä–∞—Ü–∏–æ–Ω–Ω–æ–º—É –æ–±–µ—Å–ø–µ—á–µ–Ω–∏—é –∏ —Ç.–¥.) */
export const Embedded: modeCoefficients = {
  ab: 3.6,
  bb: 1.2,
  cb: 2.5,
  db: 0.32,
  ai: 2.8,
  bi: 1.2
};

