interface Cocomo2Props { KLoC: number }

export const CocomoCalc2: React.FC<Cocomo2Props> = ({ KLoC }) => {
  
  const [drivers, setDrivers] = useState(InitialValue);
  const isMobile = useMediaQuery("(max-width: 400px)");

  const handleUpdate = (driver: string, newValue: number) =>
    setDrivers({ ...drivers, [driver]: newValue });

  const res = useMemo(() => calculateCocomo2(KLoC, drivers), [
    drivers,
    KLoC,
  ]);

  return (
    <>
       {/* <Res>
        <h3>{Round(res.PM)}</h3>
        <div className="label">–¢—Ä—É–¥–æ–µ–º–∫–æ—Å—Ç—å –≤ —á–µ–ª–æ–≤–µ–∫–æ-–º–µ—Å—è—Ü–∞—Ö</div>
        <h3>{Round(res.PM)}</h3>
        <div className="label">–¢—Ä—É–¥–æ–µ–º–∫–æ—Å—Ç—å –≤ —á–µ–ª–æ–≤–µ–∫–æ-–º–µ—Å—è—Ü–∞—Ö</div>
      </Res> */}


    <Grid1>
      <ResultColumn title={Round(res.PM)} label="–¢—Ä—É–¥–æ–µ–º–∫–æ—Å—Ç—å(PM), —á–µ–ª.√ó –º–µ—Å" />
      <ResultColumn title={Round(res.TM)} label="–í—Ä–µ–º—è —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–∏(TM), –º–µ—Å" />
    </Grid1>

      <hr />

      <Columns>
        {isMobile ? (
          <>
            <div>–ö—Ä–∏—Ç. –Ω–∏–∑.</div>
            <div>–û—á. –Ω–∏–∑.</div>
            <div>–ù–∏–∑.</div>
            <div>–°—Ä–µ–¥.</div>
            <div>–í—ã—Å.</div>
            <div>–û—á. –≤—ã—Å.</div>
            <div>–ö—Ä–∏—Ç. –≤—ã—Å.</div>
          </>
        ) : (
          <>
            <div>–ö—Ä–∏—Ç–∏—á–µ—Å–∫–∏ –Ω–∏–∑–∫–∏–π</div>
            <div>–û—á–µ–Ω—å –Ω–∏–∑–∫–∏–π</div>
            <div>–ù–∏–∑–∫–∏–π</div>
            <div>–°—Ä–µ–¥–Ω–∏–π</div>
            <div>–í—ã—Å–æ–∫–∏–π</div>
            <div>–û—á–µ–Ω—å –≤—ã—Å–æ–∫–∏–π</div>
            <div>–ö—Ä–∏—Ç–∏—á–µ—Å–∫–∏ –≤—ã—Å–æ–∫–∏–π</div>
          </>
        )}
      </Columns>

      <Grid>
        {driversKeys.map(({ key, text }) => (
          <RadioGroupCocomo2
            title={text}
            selectRadio={handleUpdate}
            value={drivers[key]}
            radioValues={costDriversCocomo2[key]}
            group={key}
            key={key}
          />
        ))}
      </Grid>
    </>
  );
};

