import {
  modeCoefficients,
  Organic,
  Semidetach,
  Embedded,
  costDrivers
} from "cocomo";
import { createModuleResolutionCache } from "typescript";
import { costDriversCocomo2, costDriversCocomo2Advance, ratingFactor, ratingFactorCocomo2, ratingFactorCocomo2Advance } from "./interface";

// –ë–∞–∑–æ–≤—ã–µ —É—Ä–∞–≤–Ω–µ–Ω–∏—è COCOMO:

// –¢—Ä—É–¥–æ–µ–º–∫–æ—Å—Ç—å = ab(KLoC)bb [—á–µ–ª–æ–≤–µ–∫–æ-–º–µ—Å—è—Ü–µ–≤]
// –°—Ä–æ–∫ —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–∏ –∏–ª–∏ –¥–ª–∏—Ç–µ–ª—å–Ω–æ—Å—Ç—å = cb(–¢—Ä—É–¥–æ–µ–º–∫–æ—Å—Ç—å)db [–º–µ—Å—è—Ü–µ–≤]
// –ß–∏—Å–ª–æ —Ä–∞–∑—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤ = –¢—Ä—É–¥–æ–µ–º–∫–æ—Å—Ç—å/ –°—Ä–æ–∫ —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–∏ [—á–µ–ª–æ–≤–µ–∫]

/** –¢—Ä—É–¥–æ–µ–º–∫–æ—Å—Ç—å = ab(KLoC)bb [—á–µ–ª–æ–≤–µ–∫–æ-–º–µ—Å—è—Ü–µ–≤] */
export const personMonths = ({ ab, bb }: modeCoefficients, KLoC: number) =>
  ab * KLoC ** bb;

/** –°—Ä–æ–∫ —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–∏ –∏–ª–∏ –¥–ª–∏—Ç–µ–ª—å–Ω–æ—Å—Ç—å = cb(–¢—Ä—É–¥–æ–µ–º–∫–æ—Å—Ç—å)db [–º–µ—Å—è—Ü–µ–≤] */
export const months = (coefficients: modeCoefficients, KLoC: number) =>
  coefficients.cb * personMonths(coefficients, KLoC) ** coefficients.db;

/** –ß–∏—Å–ª–æ —Ä–∞–∑—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤ = –¢—Ä—É–¥–æ–µ–º–∫–æ—Å—Ç—å/ –°—Ä–æ–∫ —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–∏ [—á–µ–ª–æ–≤–µ–∫] */
export const persons = (team: modeCoefficients, KLoC: number) =>
  !KLoC ? 0 : personMonths(team, KLoC) / months(team, KLoC);

/** –ü—Ä–∏–Ω–∏–º–∞–µ—Ç —Ç–∏–ø –∫–æ–º–∞–Ω–¥—ã, –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ç—ã—Å—è—á —Å—Ç—Ä–æ–∫ –∫–æ–¥–∞ –∏ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –æ–±—ä–µ–∫—Ç
 *  —Å —Ä–∞—Å—Å—á–∏—Ç–∞–Ω—ã–º–∏ —Ç—Ä—É–¥–æ—ë–º–∫–æ—Å—Ç—å—é, —Å—Ä–æ–∫–æ–º —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–∏ –≤ –º–µ—Å—è—Ü–∞—Ö
 *  –∏ —Ä–µ–∫–æ–º–º–µ–Ω–¥—É–µ–º–æ–µ —á–∏—Å–ª–æ —Ä–∞–∑—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤ */
export const calculateBasicCocomo = (
  team: "organic" | "semidetach" | "embedded",
  KLoC: number
) => {
  const coefficients = getMode(team);
  return {
    personMonths: personMonths(coefficients, KLoC),
    months: months(coefficients, KLoC),
    persons: persons(coefficients, KLoC)
  };
};

/** –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –∫–æ—ç—Ñ—Ñ–∏—Ü–∏–µ–Ω—Ç—ã –æ–¥–Ω–æ–≥–æ –∏–∑ —Ç—Ä—ë—Ö –±–∞–∑–æ–≤—ã—Ö —Ç–∏–ø–æ–≤ –∫–æ–º–∞–Ω–¥ */
const getMode = (team: "organic" | "semidetach" | "embedded") => {
  if (team === "organic") return Organic;
  if (team === "semidetach") return Semidetach;
  if (team === "embedded") return Embedded;
  throw new Error(
    `–ü–µ—Ä–µ–¥–∞–Ω–Ω—ã–π —Ä–µ–∂–∏–º '${team}' –Ω–µ —Å–æ–æ—Ç–≤–µ—Ç—Å–≤—É–µ—Ç –æ–¥–Ω–æ–º—É –∏–∑ —Ç—Ä—ë—Ö –±–∞–∑–æ–≤—ã—Ö —Ç–∏–ø–æ–≤!`
  );
};

/** E = ai * (KLoC ^ bi) * –†–§–¢
 *  E ‚Äì —Ç—Ä—É–¥–æ–µ–º–∫–æ—Å—Ç—å —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–∏ –ü–û –≤ —á–µ–ª–æ–≤–µ–∫–æ-–º–µ—Å—è—Ü–∞—Ö,
 *  KLoC ‚Äì –æ—Ü–µ–Ω–æ—á–Ω—ã–π —Ä–∞–∑–º–µ—Ä –ø—Ä–æ–≥—Ä–∞–º–º—ã –≤ —Ç—ã—Å—è—á–∞—Ö —Å—Ç—Ä–æ–∫–∞—Ö –∏—Å—Ö–æ–¥–Ω–æ–≥–æ –∫–æ–¥–∞,
 *  –†–§–¢ ‚Äì —Ä–µ–≥—É–ª–∏—Ä—É—é—â–∏–π —Ñ–∞–∫—Ç–æ—Ä, —Ä–∞—Å—Å—á–∏—Ç–∞–Ω–Ω—ã–π —Ä–∞–Ω–µ–µ.
 */
export const calculateIntermediateCocomo = (
  team: "organic" | "semidetach" | "embedded",
  KLoC: number,
  drivers: ratingFactor
) => {
  const { ai, bi } = getMode(team);
  const values = Object.entries(drivers).map(
    ([key, value]) => costDrivers[key][value - 1]
  );

  const RFT: number = values.reduce(Multiply, 1);

  const PM = ai * KLoC ** bi * RFT;

  const coefficients = getMode(team);
  const TM = coefficients.cb * PM ** coefficients.db

  // ùë∑ùë¥ = ùíÇùíä √ó (ùë∫ùë∞ùíÅùë¨)ùíÉùíä,
  // ùëªùë¥ = ùíÑùíä √ó (ùë∑ùë¥)ùíÖùíä,
  
  return {
    PM: PM,
    TM: TM
  }
};

const Multiply = (total: number, value: number) => total * value;


// Cocomo2

export const calculateCocomo2 = (
  KLoC: number,
  drivers: ratingFactorCocomo2
) => {
  const values = Object.entries(drivers).map(
    ([key, value]) => costDriversCocomo2[key][value-1]
  );
  const EAF: number = values.slice(0, 7).reduce(Multiply, 1);
  const EAF1: number = values.slice(0, 6).reduce(Multiply, 1);
  const reducer = (accumulator, currentValue) => accumulator + currentValue;
  const SF: number = values.slice(7).reduce(reducer);
  const SCED: number = values[6]
  console.log(SCED)
  const SIZE = KLoC
  const A = 2.94
  const B = 0.91
  const E = B + (0.01 * SF)
  const PM = EAF*A*(Math.pow(SIZE,E))
  const PMforTM = EAF1*A*(Math.pow(SIZE,E))
  const TM = SCED * 3.67 * (Math.pow(PMforTM,(0.28 + 0.2*(E-B))))
  return {
    PM: PM,
    TM: TM
  }
};

export const calculateCocomo2Advance = (
  KLoC: number,
  drivers: ratingFactorCocomo2Advance
) => {

  const values = Object.entries(drivers).map(
    ([key, value]) => costDriversCocomo2Advance[key][value-1]
  );
  const EAF: number = values.slice(0, 17).reduce(Multiply, 1);
  const EAF1: number = values.slice(0, 16).reduce(Multiply, 1);
  const reducer = (accumulator, currentValue) => accumulator + currentValue;
  const SF: number = values.slice(17).reduce(reducer);
  const SCED: number = values[16]
  const SIZE = KLoC
  const B = 0.91
  const A = 2.45
  const E = B + (0.01 * SF)
  const PM = EAF*A*(Math.pow(SIZE,E))
  const PMforTM = EAF1*A*(Math.pow(SIZE,E))
  const TM = SCED * 3.67 * (Math.pow(PMforTM,0.28 + 0.2*(E-B)))
  return {
    PM: PM,
    TM: TM
  }
};