interface IntermediateCocomoProps {
  team: "organic" | "semidetach" | "embedded";
  KLoC: number;
}
export const IntermediateCocomo: React.FC<IntermediateCocomoProps> = ({
  team,
  KLoC,
}) => {
  const [drivers, setDrivers] = useState(InitialValue);
  const isMobile = useMediaQuery("(max-width: 400px)");

  const handleUpdate = (driver: string, newValue: number) =>
    setDrivers({ ...drivers, [driver]: newValue });

  const res = useMemo(() => calculateIntermediateCocomo(team, KLoC, drivers), [
    drivers,
    team,
    KLoC,
  ]);

  return (
    <>
    <Grid1>
      <ResultColumn title={Round(res.PM)} label="–¢—Ä—É–¥–æ–µ–º–∫–æ—Å—Ç—å(PM), —á–µ–ª.√ó –º–µ—Å" />
      <ResultColumn title={Round(res.TM)} label="–í—Ä–µ–º—è —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–∏(TM), –º–µ—Å" />
    </Grid1>

      <hr />

      <Columns>
        {isMobile ? (
          <>
            <div>–û—á. –Ω–∏–∑.</div>
            <div>–ù–∏–∑.</div>
            <div>–°—Ä–µ–¥.</div>
            <div>–í—ã—Å.</div>
            <div>–û—á. –≤—ã—Å.</div>
            <div>–ö—Ä–∏—Ç.</div>
          </>
        ) : (
          <>
            <div>–û—á–µ–Ω—å –Ω–∏–∑–∫–∏–π</div>
            <div>–ù–∏–∑–∫–∏–π</div>
            <div>–°—Ä–µ–¥–Ω–∏–π</div>
            <div>–í—ã—Å–æ–∫–∏–π</div>
            <div>–û—á–µ–Ω—å –≤—ã—Å–æ–∫–∏–π</div>
            <div>–ö—Ä–∏—Ç–∏—á–µ—Å–∫–∏–π</div>
          </>
        )}
      </Columns>

      <Grid>
        {driversKeys.map(({ key, text }) => (
          <RadioGroup
            title={text}
            selectRadio={handleUpdate}
            value={drivers[key]}
            radioValues={costDrivers[key]}
            group={key}
            key={key}
          />
        ))}
      </Grid>
    </>
  );
};

